import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, warning } from "../Alert";
import { AxiosMasterPost } from "../Axios";
import HeaderCard from "../HeaderCard";
import FormSaldoAwalCT from "./Component/FormSaldoAwalCT";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
class SaldoAwalCT extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
      listSales: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
  }
  handleChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handlerSimpan(data) {
    if (data.tanggal === undefined) {
      warning("Tanggal Tidak boleh kosong");
      return false;
    }
    console.log(data);
    var isi = {
      no_transaksi: this.props.noFaktur,
      jenis_trx: "CT",
      tanggal: data.tanggal,
      kode_lokasi: "-",
      detail_saldo_awal: [
        {
          kode_supplier: "-",
          kode_customer: "-",
          kode_sales: "-",
          no_bon: "-",
          saldo_gr: 0,
          saldo_rp: 0,
          titip_gr: 0,
          titip_rp: 0,
          kode_jenis: data.kode_kategori,
          kadar_cetak: "0",
          bruto: data.bruto,
          netto: data.netto,
        },
      ],
    };
    console.log(JSON.stringify(isi));
    AxiosMasterPost("saldo-awal/simpan", isi)
      .then(() => this.props.dispatch(reset("FormSaldoAwalCT")))
      .then(() => berhasil("Berhasil Simpan Saldo Awal"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => this.props.dispatch(getUUID()))
      .catch((err) =>
        warning(`Gagal Simpan Saldo Awal, Detial : ${err.response.data}`)
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/saldoawal">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Saldo Awal CT/CK" />
              <div className="card-body">
                <div className="col-lg-12 mt-3">
                  <FormSaldoAwalCT
                    onSubmit={(data) => this.handlerSimpan(data)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

SaldoAwalCT = reduxForm({
  form: "SaldoAwalCT",
  enableReinitialize: true,
})(SaldoAwalCT);
export default connect(maptostate, null)(SaldoAwalCT);
