import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getListBarang } from "../../actions/kodeBarangAction";
import { renderField, renderFieldSelect } from "../../renderField";
import validasiSaldoAwalCT from "../../Validations/validasiSaldoAwalCT";

class FormSaldoAwalCT extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              label="Tanggal"
              name="tanggal"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="kode_kategori"
              component={renderFieldSelect}
              options={this.props.KodeBarangList.filter(
                (list) =>
                  list.kode_kelompok === "CIOK" ||
                  list.kode_kelompok === "RONGSOK"
              ).map((data) => {
                let hasil = {
                  value: data.kode_jenis,
                  name: data.nama_jenis,
                };
                return hasil;
              })}
              type="text"
              label="Kode Kategori"
              placeholder="Masukan Kode Kategori"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Netto"
              name="netto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Bruto"
              name="bruto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalCT = reduxForm({
  form: "FormSaldoAwalCT",
  enableReinitialize: true,
  validate: validasiSaldoAwalCT,
})(FormSaldoAwalCT);
export default connect((state) => {
  return {
    KodeBarangList: state.provinsi.kodeBarang,
  };
})(FormSaldoAwalCT);
